import React from 'react'
import s from './styles.module.scss'
import { URLCDN } from '@/constant'
import classnames from 'classnames'
import qqloginicon from '@/static/qqloginicon.png'
class Login extends React.Component {
    constructor(props) {
        super(props)
        this.state = {}
    }

    onClick = () => {
        const { onClick } = this.props
        onClick && onClick()
    }

    cancel = () => {
        const { setProps } = this.props
        setProps && setProps('showLogin', false)
    }

    login = () => {
        window.location.href = 'https://graph.qq.com/oauth2.0/authorize?client_id=101891448&response_type=token&scope=all&redirect_uri=http%3A%2F%2F127.0.0.1:3001%2Findex.html'
        // window.open(
        //     'https://graph.qq.com/oauth2.0/authorize?client_id=101891448&response_type=token&scope=all&redirect_uri=http%3A%2F%2F127.0.0.1:3001%2Findex.html',
        //     'oauth2Login_10556',
        //     'height=525,width=585, toolbar=no, menubar=no, scrollbars=no, status=no, location=yes, resizable=yes'
        // )
    }

    render() {
        const { show } = this.props
        let coverModal = classnames({
            [s.coverModal]: true,
            [s.show]: show,
        })
        let container = classnames({
            [s.container]: true,
            [s.containerShow]: show,
        })
        return (
            <div className={coverModal}>
                <div className={container}>
                    <p>登录后体验模版分享、云同步</p>
                    <button className={s.button} onClick={this.login}>
                        <img
                            src={qqloginicon}
                            alt="QQ登录"
                            title="QQ登录"
                        />
                        QQ登录
                    </button>
                    <span onClick={this.cancel}>暂不登录</span>
                </div>
            </div>
        )
    }
}

export default Login
