import React from 'react';
import s from './styles.module.scss';
class Yiyan extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            from: '',
            who: '',
            content: '',
        };
    }

    componentDidMount() {
        fetch('https://v1.hitokoto.cn/?c=h&c=d&c=i&c=k&encode=json')
            .then((res) => {
                return res.json();
            })
            .then((data) => {
                let from = data.from === 'null' ? '' : data.from;
                let who = data.from_who === 'null' ? '' : data.from_who;
                let content = data.hitokoto === 'null' ? '' : data.hitokoto;
                if (from === who) {
                    who = '';
                }
                this.setState({
                    from,
                    who,
                    content,
                });
            });
    }
    render() {
        const { from, who, content } = this.state;
        return (
            <div className={s.yiyan}>
                {content && <p>『  {content}』</p>}
                {(who || from) && <div className={s.from}>
                    — — {who} 「{from}」
                </div>}
            </div>
        );
    }
}

export default Yiyan;
