import React from 'react';
import s from './styles.module.scss';
import classnames from 'classnames';
// import anime from "animejs/lib/anime.es.js";
import _ from 'lodash';
import Jsonp from 'jsonp';
import {defaultSearch} from '@/config';

class Search extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            target: localStorage.getItem('searchTarget') || 'bd',
            focus: false,
            showTagBox: false,
            suggestion: [],
            highlight: -1,
            searchValue: '',
            showSuggestion: false,
            searchType: null,
        };
        this.searchRef = React.createRef();
        this.onChangeDebounce = _.debounce(this.onChange, 100);
    }

    resetSearch = () => {
        this.searchRef.current.value = '';
        this.props.focusCallback('focus', false);
        this.setState({
            focus: false,
            showTagBox: false,
            suggestion: [],
            searchValue: '',
            showSuggestion: false,
            highlight: -1,
            searchType: null,
        });
    };

    onFocus = () => {
        this.setState({
            focus: true,
        });
        this.props.focusCallback('focus', true);
        let val = this.searchRef.current.value;
        this.onChange(val);
        document.body.addEventListener('keydown', this.search);
        let viewHeight = window.innerHeight;
        document.body.style.height = viewHeight + 'px'
    };

    onBlur = () => {
        this.setState({
            focus: false,
        });
        this.props.focusCallback('focus', false);
        document.body.removeEventListener('keydown', this.search);
        document.body.style.height =''
    };

    toTarget = (value) => {
        const { target } = this.state;
        this.resetSearch();
        window.open(defaultSearch[target].url + value);
    };

    toFanyi = (value) => {
        this.resetSearch();
        window.open('https://fanyi.baidu.com/#zh/en/' + value);
    };

    toSearchType = (value) => {
        const {searchType} = this.state;
        this.resetSearch();
        window.open(defaultSearch[searchType].url + value);
    }

    search = (e) => {
        if (e.keyCode === 13) {
            const {searchType} = this.state;
            let searchDom = this.searchRef.current;
            let value = searchDom.value && searchDom.value.trim();
            if (!value) return;
            if (searchType) {
                this.toSearchType(value);
            }else if (this.state.highlight === 0) {
                this.toFanyi(value);
            } else {
                this.toTarget(value);
            }
        }
    };

    changeTag = () => {
        const { showTagBox } = this.state;
        if (!showTagBox) {
            document.body.addEventListener('click', this.hideTargetBox);
        } else {
            document.body.removeEventListener('click', this.hideTargetBox);
        }
        this.setState({
            showTagBox: !showTagBox,
            suggestion: [],
            highlight: -1,
            showSuggestion: false,
        });
    };

    hideTargetBox = (e) => {
        if (e.target.id === 'searchTarget') {
            document.body.removeEventListener('click', this.hideTargetBox);
            return;
        }

        document.body.removeEventListener('click', this.hideTargetBox);
        this.setState({
            showTagBox: false,
        });
    };

    setTag = (tag) => {
        localStorage.setItem('searchTarget', tag);
        this.searchRef.current.focus();
        this.setState({
            target: tag,
            showTagBox: false,
            searchType: null,
            focus: true
        });
    };

    addScriptTag(src) {
        var script = document.createElement('script');
        script.setAttribute('type', 'text/javascript');
        script.src = src;
        document.body.appendChild(script);
    }

    isSearchType = (val) => {
        let valArr = val.split(' ');
        let searchType = valArr[0];
        if (valArr.length > 1 && defaultSearch[valArr[0]]) {
            val = val.replace(searchType+' ','');
            this.searchRef.current.value = val;
            if (defaultSearch[searchType].main) {
                this.setTag(searchType);
                this.setState({
                    searchType: null,
                    searchValue: val,
                    showSuggestion: false,
                })
                return true
            }
            this.setState({
                searchType: valArr[0],
                searchValue: val,
                showSuggestion: false,
            })
            return true
        }
        return false
    }

    onChange = (val) => {
        if (this.isSearchType(val)) return;
        val = (val && val.trim()) || '';
        if (val) {
            Jsonp(
                'http://suggestion.baidu.com/su?wd=' +
                    val +
                    '&cb=window.baidu.sug',
                {},
                function (res, data) {}
            );

            this.setState({
                searchValue: val,
                showSuggestion: true,
            });
        } else {
            this.setState({
                suggestion: [],
                showSuggestion: false,
                searchValue: '',
            });
        }
    };

    searchChange = (e) => {
        e.persist();
        this.onChangeDebounce(e.target.value);
    };

    renderSuggestion() {
        let { suggestion, highlight, searchValue, showSuggestion } = this.state;
        if (!showSuggestion) return null;
        let arr = [];
        let dataArr = [];
        if (searchValue) {
            dataArr = ['翻译：' + searchValue, ...suggestion];
        }
        dataArr.map((item, index) => {
            let rowStyle = classnames({
                [s.highlight]: highlight === index,
                [s.suggestionRow]: true,
                suggestionRow: true,
            });
            arr.push(
                <div
                    className={rowStyle}
                    data-id='suggestion'
                    onClick={
                        index !== 0
                            ? this.toTarget.bind(null, item)
                            : this.toFanyi.bind(null, item.substr(3))
                    }
                    key={item}
                >
                    {item}
                </div>
            );
            return arr;
        });
        return <div className={s.suggestion}>{arr}</div>;
    }

    hideSuggestion = (e) => {
        let suggestionId = _.get(e, 'target.dataset.id', '');
        let id = _.get(e, 'target.id', '');
        if (
            id !== 'search' &&
            suggestionId !== 'suggestion' &&
            this.state.showSuggestion
        ) {
            this.setState({
                showSuggestion: false,
                suggestion: [],
                highlight: -1,
            });
        }
    };

    changeSuggestion = (e) => {
        const { autoFocus } = this.props;
        if (!autoFocus) return;

        let { suggestion, highlight, focus, showSuggestion } = this.state;
        let len = suggestion.length + 1;
        let _this = this;
        function changeInput(index) {
            let doms = document.getElementsByClassName('suggestionRow');
            let value = doms[index].innerText;
            if (index === 0) {
                value = value.substr(3);
            }
            _this.searchRef.current.value = value;
        }

        if (focus && e.keyCode === 27) {
            this.setState({
                focus: false,
                showSuggestion: false,
            });
            this.searchRef.current.blur();
        }
        if (showSuggestion && focus) {
            if (e.keyCode === 40) {
                let index = (highlight + 1) % len;
                changeInput(index);
                this.setState({
                    highlight: index,
                });
                e.preventDefault();
            } else if (e.keyCode === 38) {
                if (highlight <= 0) {
                    highlight = len;
                }
                changeInput(highlight - 1);
                this.setState({
                    highlight: highlight - 1,
                });
                e.preventDefault();
            }
        }

        if (!focus) {
            this.setState({
                focus: true,
            });
            this.searchRef.current.focus();
        }
    };

    componentDidMount() {
        // let str = '';
        // for (let key in defaultSearch) {
        //     str = str + `${defaultSearch[key].title}:"${key}", `
        // }

        // console.log(str);
        let _this = this;
        document.body.addEventListener('click', this.hideSuggestion);
        document.body.addEventListener('keydown', this.changeSuggestion);
        window.baidu = {
            sug: function (data) {
                _this.setState({
                    suggestion: data.s,
                });
            },
        };
        window.handleCallback = function (data) {
            console.log(data);
        };
    }

    componentWillUnmount() {
        document.body.removeEventListener('click', this.hideSuggestion);
        document.body.removeEventListener('keydown', this.changeSuggestion);
    }

    render() {
        const { focus, showTagBox, target, showSuggestion, searchType } = this.state;
        let focusStyle = classnames({
            [s.searchContainer]: true,
            [s.focus]: focus,
            [s.borderEx]: showTagBox,
            [s.haveSuggestion]: showSuggestion,
        });

        let tagBoxStyle = classnames({
            [s.tagBox]: true,
            [s.showTag]: showTagBox,
            [s.borderTop]: showTagBox,
        });

        let targetSearch = classnames({
            [s.targetSearch]: true,
            [s.moveLeftImg]: !!searchType
        })

        let targetMore = classnames({
            [s.targetMore]: true,
        })

        if (!defaultSearch[target]) {
            localStorage.setItem('searchTarget', 'bd')
        }

        return (
            <div className={focusStyle}>
                <div className={s.targetBtnBox}>
                    <img
                        id='target'
                        className={targetSearch}
                        src={defaultSearch[target].icon}
                        alt={target}
                        onClick={this.changeTag}
                    />
                    <span className={targetMore} onClick={this.changeTag}>{searchType && defaultSearch[searchType].title}</span>
                </div>
                <input
                    ref={this.searchRef}
                    autoComplete='off'
                    className={s.input}
                    onBlur={this.onBlur}
                    onFocus={this.onFocus}
                    onChange={this.searchChange}
                    type='text'
                />
                <div id='tagBox' className={tagBoxStyle}>
                    <div
                        className={s.tag}
                        onClick={() => {
                            this.setTag('bd');
                        }}
                    >
                        <img src={defaultSearch.bd.icon} alt='baidu' />
                        <span>{defaultSearch.bd.title}</span>
                    </div>
                    <div
                        className={s.tag}
                        onClick={() => {
                            this.setTag('gg');
                        }}
                    >
                        <img src={defaultSearch.gg.icon} alt='google' />
                        <span>{defaultSearch.gg.title}</span>
                    </div>
                    <div
                        className={s.tag}
                        onClick={() => {
                            this.setTag('bing');
                        }}
                    >
                        <img src={defaultSearch.bing.icon} alt='bing' />
                        <span>{defaultSearch.bing.title}</span>
                    </div>
                    <div
                        className={s.tag}
                        onClick={() => {
                            this.setTag('sg');
                        }}
                    >
                        <img src={defaultSearch.sg.icon} alt='sougou' />
                        <span>{defaultSearch.sg.title}</span>
                    </div>
                    <div
                        className={s.tag}
                        onClick={() => {
                            this.setTag('360');
                        }}
                    >
                        <img src={defaultSearch['360'].icon} alt='360' />
                        <span>{defaultSearch['360'].title}</span>
                    </div>
                    <div
                        className={s.tag}
                        onClick={() => {
                            this.setTag('ds');
                        }}
                    >
                        <img src={defaultSearch.ds.icon} alt='dasheng' />
                        <span>{defaultSearch.ds.title}</span>
                    </div>
                </div>

                {this.renderSuggestion()}
            </div>
        );
    }
}

export default Search;
