import React from 'react'
import s from './App.module.scss'
import Search from '@/components/Search'
import Yiyan from '@/components/Yiyan'
import ConfigBtns from '@/components/ConfigBtns'
import Background from '@/components/Background'
import { ERRORIMGURL, URLCDN, APPID } from '@/constant'
import Nav from '@/components/Nav'
import Time from '@/components/Time'
import Weather from '@/components/Weather'
import Login from '@/components/Login'
import Jsonp from 'jsonp';
import classnames from 'classnames'
import bgAllUrl from '@/mock/bgurl.json'
import { defaultNavIndexArr, defaultBg } from '@/config/index'
import navLists from './navLists.json'
import AddNav from '@/components/AddNav'
import Notification from 'rc-notification'
import { DndProvider } from 'react-dnd'
import { HTML5Backend } from 'react-dnd-html5-backend'
import { TouchBackend } from 'react-dnd-touch-backend'
import ConfigMenu from '@/components/configMenu'
import TemplateConfig from '@/components/TemplateConfig'
import * as u from '@/utils'
import myFetch from '@/api'

class App extends React.Component {
    constructor(props) {
        super(props)

        let userNavIndexArr = localStorage.getItem('userNavIndexArr')
        userNavIndexArr =
            (userNavIndexArr && JSON.parse(userNavIndexArr)) || null
        let bgUrl = localStorage.getItem('bgUrl') || ''
        let imgDBid = localStorage.getItem('imgDBid') || ''
        let videoUrl = localStorage.getItem('videoUrl') || ''

        if (imgDBid) {
            bgUrl = ERRORIMGURL
        }

        this.state = {
            focus: false,
            showBgConfig: false,
            bgUrl,
            imgDBid,
            videoUrl,
            bgAllUrl: [],
            showAddNav: false,
            userNavIndexArr: userNavIndexArr || defaultNavIndexArr,
            editIndex: null,
            showTemplateConfig: false,
            showConfigMenu: false,
            templateData: null,
            showLogin: false,
            userName: '',
            userIcon: '',
        }
        this.bgImgRef = React.createRef()
        this.app = React.createRef()
        this.setStateCallback = this.setStateCallback.bind(this)
    }

    getNavLists = (navs) => {
        let { templateData } = this.state
        let arr = []
        let userNav = localStorage.getItem('userNav')
        userNav = (userNav && JSON.parse(userNav)) || {}
        if (templateData) {
            userNav = templateData.userNav
        }
        let allNav = { ...navLists, ...userNav }
        navs.map((item) => {
            return allNav[item] && arr.push(allNav[item])
        })

        return arr
    }

    onError = (err) => {
        this.changeBg(URLCDN + '/bg/' + defaultBg)
        // err.target.src = ERRORIMGURL;
    }

    /**
     * 改变背景url， imgDBid 为用户上传图片id
     */
    changeBg = (url, video, imgDBid, file) => {
        const { bgUrl, videoUrl } = this.state
        if (video === true && url !== videoUrl) {
            this.setStorage('bgUrl')
            this.setStorage('videoUrl', url)
            this.setStorage('imgDBid')
            this.setState({
                bgUrl: null,
                videoUrl: url,
                imgDBid: null,
                bgImgFile: null,
            })
        } else if (url !== bgUrl) {
            if (this.bgImgRef.current)
                this.bgImgRef.current.style.filter = 'blur(6px)'
            if (!imgDBid) {
                this.setStorage('bgUrl', url)
                this.setStorage('videoUrl')
                this.setStorage('imgDBid')
                this.setState({
                    bgUrl: url,
                    videoUrl: null,
                    bgImgFile: null,
                    imgDBid: null,
                })
            } else {
                this.setStorage('bgUrl', url)
                this.setStorage('imgDBid', imgDBid)
                this.setStorage('videoUrl')
                this.setState({
                    bgUrl: url,
                    videoUrl: null,
                    bgImgFile: file,
                    imgDBid,
                })
            }
        }
    }

    bgImgLoad = (e) => {
        const { bgUrl } = this.state
        let src = e.target.src

        if (src.indexOf('/thumbnail/') < 0) {
            e.target.style.filter = 'blur(0px)'
        } else if (bgUrl) {
            e.target.src = bgUrl
        } else {
            e.target.src = URLCDN + '/bg/' + defaultBg
        }
    }

    checkClick = (e) => {
        const { showBgConfig } = this.state

        if (showBgConfig) {
            let bodyWidht = document.body.offsetWidth
            if (e.clientX < bodyWidht - 340) {
                this.setState({
                    showBgConfig: false,
                })
            }
        }
    }

    componentDidMount() {
        const { imgDBid } = this.state
        document.oncontextmenu = () => false
        document.body.addEventListener('click', this.checkClick)

        let request = window.indexedDB.open('uploadDB')
        if (imgDBid) {
            request.onupgradeneeded = (event) => {
                window.db = event.target.result
                let objectStore
                if (!window.db.objectStoreNames.contains('uploadTable')) {
                    objectStore = window.db.createObjectStore('uploadTable', {
                        keyPath: 'id',
                    })
                    objectStore.createIndex('file', 'file', { unique: false })
                }
            }

            request.onerror = function (event) {
                console.log('数据库打开报错1')
            }

            request.onsuccess = (event) => {
                window.db = request.result
                let imgItem = window.db
                    .transaction(['uploadTable'], 'readwrite')
                    .objectStore('uploadTable')
                    .get(+imgDBid)

                imgItem.onerror = function (event) {
                    console.log('事务失败')
                }

                imgItem.onsuccess = (event) => {
                    if (imgItem.result) {
                        let src = window.URL.createObjectURL(
                            imgItem.result.file
                        )
                        this.setState({
                            bgImgFile: imgItem.result.file,
                            imgDBid: imgItem.result.id,
                            bgUrl: src,
                            videoUrl: null,
                        })
                    } else {
                        this.setStorage('bgUrl')
                        this.setStorage('videoUrl')
                        this.setStorage('imgDBid')
                        this.setState({
                            imgDBid: null,
                            bgUrl: null,
                            videoUrl: null,
                        })
                    }
                }
            }
        }

        Notification.newInstance(
            {
                style: { top: 35, left: '50%', transform: 'translateX(-50%)' },
                prefixCls: 'notification',
                maxCount: 1,
            },
            (n) => (window.notification = n)
        )
        if (window.location.search.indexOf('tmp') >= 0) {
            let name = u.getSearch('tmp')
            if (name) {
                fetch(`/api/v1/template/get?name=${name}`, {})
                    .then((res) => res.json())
                    .then((data) => {
                        console.log(data)
                        this.setState({
                            templateData: data,
                        })
                    })
            }
        }
        let token = localStorage.getItem('token');
        let userName = localStorage.getItem('userName');
        let userIcon = localStorage.getItem('userIcon');
        if (window.location.hash.indexOf('access_token') >= 0) {
            let access_token = u.getHash('access_token')
            window.history.replaceState(null,null,'/')
            if (access_token) {
                this.setStorage('token',access_token)
                token = access_token
            }
            Jsonp(
                `https://graph.qq.com/oauth2.0/me?access_token=${token}`,
                {},
            );
            window.callback = (data)=> {
                if (!data.error) {
                    myFetch('/api/v1/user/login',{
                        method: 'post',
                        body: JSON.stringify({
                            appid:APPID,
                            token:token,
                            openid:data.openid
                        }),
                    })
                        .then((res) => res.json())
                        .then((res) => {
                            this.setState({
                                userName: res.name,
                                userIcon: res.icon
                            })
                            this.setStorage('userName',res.name);
                            this.setStorage('userIcon',res.icon);
                        })
                }else if (data.error === 100016){
                    console.log(data.error_description);
                }
            }
        }

        if (userName && userIcon) {
            this.setState({
                userName,
                userIcon
            })
        }

    }

    setStorage = (key,val) => {
        if (val === undefined) {
            localStorage.removeItem(key)
        }else {
            localStorage.setItem(key,val)
        }
    }

    randBg = () => {
        let len = bgAllUrl.length
        if (len) {
            if (this.bgImgRef.current)
                this.bgImgRef.current.style.filter = 'blur(6px)'
            let url =
                URLCDN + '/bg/' + bgAllUrl[Math.floor(Math.random() * len)]
            this.setState({
                bgUrl: url,
                videoUrl: null,
                imgDBid: null,
            })
            this.setStorage('bgUrl', url)
            this.setStorage('videoUrl')
            this.setStorage('imgDBid')
        }
    }

    setStateCallback() {
        let state = {}
        if (arguments.length === 1) {
            this.setState({
                [arguments[0]]: !this.state[arguments[0]],
            })
        } else {
            for (let i = 0; i < arguments.length; i = i + 2) {
                state[arguments[i]] = arguments[i + 1]
            }
            this.setState(state)
        }
    }

    templateCancel = () => {
        this.setState({
            templateData: null
        })
        window.history.replaceState(null,null,'/');
    }

    application = () => {
        const {templateData} = this.state
        this.setStorage('bgUrl', templateData.bgImgUrl)
        this.setStorage('videoUrl', templateData.bgVideoUrl)
        this.setStorage('userNavIndexArr', templateData.userNavIndexArr)
        this.setStorage('userNav', templateData.userNav)
        this.setState({
            templateData: null,
            bgUrl: templateData.bgImgUrl,
            imgDBid: '',
            bgImgFile: templateData.bgImgFile,
            videoUrl: templateData.bgVideoUrl,
            userNavIndexArr: templateData.userNavIndexArr,
            userNav: templateData.userNav,
            note:templateData.note
        })
    }

    render() {
        let {
            focus,
            bgUrl,
            showBgConfig,
            showAddNav,
            videoUrl,
            userNavIndexArr,
            editIndex,
            showTemplateConfig,
            showConfigMenu,
            bgImgFile,
            templateData,
            showLogin,
            userIcon,
            userName
        } = this.state

        // 模版过滤
        if (templateData) {
            bgUrl = templateData.bgImgUrl
            videoUrl = templateData.bgVideoUrl
            bgImgFile = templateData.bgImgFile
            userNavIndexArr = templateData.userNavIndexArr
            if (bgImgFile) {
                // bgUrl = window.URL.createObjectURL(bgImgFile)
                // bgUrl = bgImgFile
            }
        }
        let bgStyle = classnames({
            [s.bgbox]: true,
            [s.bgFocus]: focus,
        })
        let src = bgUrl || URLCDN + '/thumbnail/' + defaultBg
        
        let useNavs = this.getNavLists(userNavIndexArr)
        let backend =
            window.userTerminal === 'mobile' ? TouchBackend : HTML5Backend
        return (
            <DndProvider backend={backend}>
                <div className={s.App} ref={this.app}>
                    {templateData && (
                        <div className={s.template}>
                            是否应用「{templateData.name}」模版？
                            <span className={s.btn} onClick={this.templateCancel}>取消</span>
                            <span className={`${s.btn} ${s.success}`} onClick={this.application}>
                                应用
                            </span>
                        </div>
                    )}
                    <div className="imgBlur"></div>

                    {!videoUrl && (
                        <img
                            onLoad={this.bgImgLoad}
                            id="bgImg"
                            ref={this.bgImgRef}
                            src={src.replace(/\/bg\/|\/4k\//, '/thumbnail/')}
                            alt="bg"
                            onError={this.onError}
                            className={bgStyle}
                        ></img>
                    )}
                    {videoUrl && window.userTerminal !== 'mobile' && (
                        <video
                            id="videoBg"
                            className={s.videoBg}
                            src={videoUrl}
                            autoPlay={true}
                            loop={true}
                            muted={true}
                        ></video>
                    )}
                    <div className={s.cover}></div>
                    <Search
                        setStorage={this.setStorage}
                        focusCallback={this.setStateCallback}
                        autoFocus={!showAddNav && !showTemplateConfig}
                    />
                    <div className={s.navBox}>
                        <Nav
                            setProps={this.setStateCallback}
                            setStorage={this.setStorage}
                            useNavs={useNavs}
                            userNavIndexArr={userNavIndexArr}
                            templateData={templateData}
                        />
                    </div>
                    {(window.userTerminal === 'pc' || !focus) && (
                        <div className={s.yiyanBox}>
                            <Yiyan />
                        </div>
                    )}
                    {
                        <Background
                            show={!templateData && showBgConfig}
                            changeBg={this.changeBg}
                            setProps={this.setStateCallback}
                        />
                    }
                    {!templateData && !focus && (
                        <div className={s.configContainer}>
                            <ConfigBtns
                                setProps={this.setStateCallback}
                                randBg={this.randBg}
                                userIcon={userIcon}
                                userName={userName}
                            />
                        </div>
                    )}

                    <Time language="chinese" />
                    <Weather />

                    <Login setProps={this.setStateCallback} show={showLogin}/>
                    <AddNav
                        setProps={this.setStateCallback}
                        setStorage={this.setStorage}
                        show={!templateData && showAddNav}
                        editIndex={editIndex}
                        useNavs={useNavs}
                        userNavIndexArr={userNavIndexArr}
                    />

                    <TemplateConfig
                        bgImgFile={bgImgFile}
                        setProps={this.setStateCallback}
                        show={!templateData && showTemplateConfig}
                        editIndex={editIndex}
                        useNavs={useNavs}
                        userNavIndexArr={userNavIndexArr}
                    />

                    <ConfigMenu
                        show={!templateData && showConfigMenu}
                        setStorage={this.setStorage}
                        setProps={this.setStateCallback}
                    />

                    <div className={s.footer}>
                        <span className={s.beian}>© 2020 崔鹏程 ｜</span>
                        <a href="http://www.beian.miit.gov.cn" target="_blank" rel="noopener noreferrer" className={s.beian}>京ICP备20023680号</a>
                    </div>
                </div>
            </DndProvider>
        )
    }
}

export default App
