import React from 'react'
import s from './styles.module.scss'
import classnames from 'classnames'
import _ from 'lodash'
import NavItem from './NavItem'
import addsite from '@/static/addsite.png';
class Nav extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            showMenu: false,
            editNavId: null,
            editIndex: null,
        }
        this.navMenu = React.createRef()
    }

    addsite = () => {
        const { setProps } = this.props
        setProps && setProps('showAddNav', true)
    }

    onContextMenu = (e) => {
        const { userNavIndexArr, templateData } = this.props
        if (templateData) return
        this.navMenu.current.style.top = e.clientY + 6 + 'px'
        this.navMenu.current.style.left = e.clientX + 6 + 'px'
        this.navMenu.current.style.transform = 'scale(1.06)'
        setTimeout(() => {
            this.navMenu.current.style.transform = 'scale(1)'
        }, 250)
        let navid = null
        let i = 0
        let target = e.target
        while (i < 3) {
            //menu 下只有3级子元素
            let id = _.get(target, 'dataset.navid', '')
            if (id) {
                navid = id
                break
            } else {
                i++
                target = target.parentElement || {}
            }
        }
        this.setState({
            showMenu: true,
            editNavId: navid,
            editIndex: _.indexOf(userNavIndexArr, navid),
        })
    }

    onBodyClick = (e) => {
        const { showMenu } = this.state
        if (showMenu) {
            let i = 0
            let target = e.target
            while (i < 2) {
                //menu 下只有两级子元素
                let id = _.get(target, 'parentElement.dataset.id', '')
                if (id === 'clickMenu') {
                    return
                } else {
                    i++
                    target = target.parentElement || {}
                }
            }
            this.navMenu.current.style.transform = 'scale(0.5)'
            this.setState({
                showMenu: false,
            })
            setTimeout(() => {
                this.navMenu.current.style.top = ''
                this.navMenu.current.style.left = ''
            }, 300)
        }
    }

    deleteNav = () => {
        const { editNavId } = this.state
        const { setProps } = this.props

        if (editNavId) {
            let userNav = localStorage.getItem('userNav')
            let localNavIndexArr = localStorage.getItem('userNavIndexArr')
            localNavIndexArr =
                (localNavIndexArr && JSON.parse(localNavIndexArr)) || {}
            if (editNavId.indexOf('user-') >= 0) {
                userNav = (userNav && JSON.parse(userNav)) || {}
                localStorage.removeItem(
                    userNav[editNavId].title + userNav[editNavId].url
                )
                delete userNav[editNavId]
                localStorage.setItem('userNav', JSON.stringify(userNav))
            }
            _.remove(localNavIndexArr, (item) => {
                return item === editNavId
            })
            localStorage.setItem(
                'userNavIndexArr',
                JSON.stringify(localNavIndexArr)
            )
            setProps('userNavIndexArr', localNavIndexArr)
            this.setState({
                showMenu: false,
            })
        }
    }

    editNav = () => {
        const { editIndex } = this.state
        const { setProps } = this.props
        setProps('showAddNav', true, 'editIndex', editIndex)
    }

    componentDidMount() {
        document.body.addEventListener('click', this.onBodyClick)
    }

    componentWillUnmount() {
        document.body.removeEventListener('click', this.onBodyClick)

    }

    renderNav = () => {
        const { useNavs, userNavIndexArr, setProps, templateData } = this.props
        let arr = []

        useNavs.map((item, index) => {
            return arr.push(
                <NavItem
                    userNavIndexArr={userNavIndexArr}
                    setProps={setProps}
                    nav={item}
                    onContextMenu={this.onContextMenu}
                    key={item.url + item.title}
                    navid={userNavIndexArr[index]}
                />
            )
        })
        !templateData && arr.push(
            <div key={'addNav'} className={s.navItem} onClick={this.addsite}>
                <div className={s.imgBox}>
                    <img alt="" src={addsite} />
                </div>
            </div>
        )
        return arr
    }

    render() {
        const { showMenu } = this.state
        let menu = classnames({
            [s.menu]: true,
            [s.showMenu]: showMenu,
        })
        return (
            <div className={s.container}>
                {this.renderNav()}
                <div
                    data-id="clickMenu"
                    className={menu}
                    ref={this.navMenu}
                >
                    <div className={s.menuItem} onClick={this.editNav}>
                        <i className={'iconfont icon-edit'}></i>
                        <span>编辑</span>
                    </div>
                    <div className={s.menuItem} onClick={this.deleteNav}>
                        <i className={'iconfont icon-delete'}></i>
                        <span>删除</span>
                    </div>
                </div>
            </div>
        )
    }
}

export default Nav
