import React from 'react';
import s from './styles.module.scss';
import wallpapers from '@/static/wallpapers.svg'
import rand from '@/static/rand.svg'
// import setting6 from '@/static/setting6.svg'
import login2 from '@/static/login2.svg'
class ConfigBtns extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    changeBg = () => {
        const {setProps} = this.props;
        setProps && setProps('showBgConfig', true)
    }

    randBg = () => {
        const {randBg} = this.props;
        randBg && randBg()
    }

    // showConfigMenu = () => {
    //     const {setProps} = this.props;
    //     // let user = localStorage.getItem('user');
    //     // if (!user) {
    //     //     setProps && setProps('showLogin', true)
    //     //     return
    //     // }
    //     setProps && setProps('showConfigMenu')
    // }

    login = () => {
        const {setProps} = this.props;
        let userIcon = localStorage.getItem('userIcon');
        if (!userIcon) {
            setProps && setProps('showLogin', true)
            return
        }else {
            setProps && setProps('showConfigMenu')
        }
    }

    render() {
        const {userIcon, userName} = this.props;
        return (
            <div className={s.container}>
                <div>
                    <img title="更换背景" alt="更换背景" src={wallpapers} onClick={this.changeBg}></img>
                </div>
                <div>
                    <img className={s.rotate} title="随机背景" alt="随机背景" src={rand} onClick={this.randBg}></img>
                </div>
                {/* <div>
                    <img data-id="setBtn" className={s.rotate} title="设置" alt="设置" src={setting6} onClick={this.showConfigMenu}></img>
                </div> */}
                <div>
                    <img className={userIcon && s.userIcon} title={userName || '登录'} alt={userName || '登录'} src={userIcon || login2} onClick={this.login}></img>
                </div>
            </div>
        );
    }
}

export default ConfigBtns;
