import React from 'react';
import s from './styles.module.scss';
import classnames from 'classnames';
// import _ from 'lodash';

class Modal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentShow: false,
            displayBlock: false,
            show: props.show,
            key: 0,
        };

        // this.throttleMouseMove = _.throttle(this.onMouseMove,16);
    }

    onClose = () => {
        const { setProps } = this.props;
        setProps && setProps('showAddNav', false, 'editIndex', null, 'showTemplateConfig', false);
        this.setState({ key: this.state.key + 1 });
    };

    // onMouseMovePersist = (e) => {
    //     e.persist();
    //     this.throttleMouseMove(e)
    // }

    /**
     * 3D 看板 动画
     */
    // onMouseMove = (e) => {
    //     let moveForce = 30;
    //     let rotateForce = 20;
    //     let docX = document.body.offsetWidth;
    //     let docY = document.body.offsetHeight;
    //     let moveX = (e.pageX - docX/2) / (docX/2) * -moveForce;
    //     let moveY = (e.pageY - docY/2) / (docY/2) * -moveForce;
    //     let rotateY = (e.pageX / docX * rotateForce*2) - rotateForce;
    //     let rotateX = -((e.pageY / docY * rotateForce*2) - rotateForce);
    //     let dom = document.getElementById('modal3D');
    //     dom.style.left = moveX+'px';
    //     dom.style.top = moveY+'px';
    //     dom.style.transform = 'rotateX('+rotateX+'deg) rotateY('+rotateY+'deg)';
    // }

    // static getDerivedStateFromProps(props, state) {
    //     if (props.show !== state.show) {
    //       return {
    //         show: props.shwo
    //       };
    //     }

    //     return null;
    //   }

    componentWillReceiveProps(nextProps) {
        if (this.props.show !== nextProps.show) {
            if (nextProps.show) {
                this.setState({
                    displayBlock: true,
                });
                setTimeout(() => {
                    this.setState({
                        currentShow: true,
                    });
                }, 10);
            } else {
                this.setState({
                    currentShow: false,
                });
                setTimeout(() => {
                    this.setState({
                        displayBlock: false,
                    });
                }, 450);
            }
        }
    }

    render() {
        const { displayBlock, currentShow, key } = this.state;
        const { children, title } = this.props;
        let container = classnames({
            [s.container]: true,
            [s.show]: currentShow,
            [s.displayBlock]: displayBlock,
        });

        let coverModal = classnames({
            [s.coverModal]: true,
            [s.show]: currentShow,
            [s.coverModalDisplay]: displayBlock,
        });
        return (
            <div className={coverModal}>
                <div className={s.modal3DBox}>
                    <div id='modal3D' className={container}>
                        <div onClick={this.onClose} className={s.close}>
                            <span className={s.closeBtn}></span>
                        </div>
                        <header className={s.header}>{title}</header>
                        <div key={key} className={s.content}>
                            {children}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

Modal.defaultProps = {
    show: false,
};

export default Modal;
