import React from 'react';
import s from './styles.module.scss';
import bgAllUrl from '@/mock/bgurl.json';
import HDurl from '@/mock/HDurl.json';
import videoUrl from '@/mock/videoUrl.json';
import classnames from 'classnames';
import { URLCDN } from '@/constant';
import { Info } from '../notification';
import library from '@/static/library.svg'
import hd from '@/static/4k1.svg'
import video1 from '@/static/video1.svg'
import uploading2 from '@/static/uploading2.svg'
import uploading from '@/static/uploading.svg'
import _ from 'lodash';
const MORE_LOAD = 6;
class Config extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            scrollIndex: 0,
            videoScrollIndex: 0,
            HDScrollIndex:0,
            showImgCount: 6,
            targetIndex: 0,
            uploadImgs: [],
            lastId: 0,
        };
    }

    changeBg = (src, video, imgDBid, file) => {
        const { changeBg } = this.props;
        changeBg && this.props.changeBg(src, video, imgDBid, file);
    };

    onError = (err) => {
        err.target.parentNode.parentNode.removeChild(err.target.parentNode);
    };

    onVideoError = (err) => {
        err.target.parentNode.parentNode.removeChild(err.target.parentNode);
    };

    onMouseOver = (e) => {
        e.target.play();
    };

    onMouseOut = (e) => {
        e.target.pause();
    };

    onScroll = (e) => {
        const { scrollIndex, videoScrollIndex, targetIndex, HDScrollIndex} = this.state;
        let scrollDom = document.getElementById('bgConfig');
        let scrollTop = e.target.scrollTop;
        if (scrollDom.offsetHeight - e.target.offsetHeight - scrollTop < 300) {
            if (targetIndex === 2) {
                this.setState({
                    videoScrollIndex: videoScrollIndex + 1,
                });
            } else if (targetIndex === 0){
                this.setState({
                    scrollIndex: scrollIndex + 1,
                });
            } else if (targetIndex === 1){
                this.setState({
                    HDScrollIndex: HDScrollIndex + 1,
                });
            }
        }
    };

    renderUploadImgs = () => {
        const { uploadImgs } = this.state;
        let arr = [];
        uploadImgs.map((item) => {
            let src = item.src;
            let id = item.id;
            let file = item.file

            return arr.push(
                <div
                    className={s.uploadingBox}
                    id={'uploadingImg-' + id}
                    key={id}
                >
                    <img
                        className={s.bgImg}
                        src={src}
                        alt='自定义壁纸'
                        onClick={this.changeBg.bind(null, src, null, id, file)}
                    />
                    <div className={s.overlayer}></div>
                    <i
                        onClick={this.deleteImgFromDB.bind(null, id)}
                        className='iconfont icon-delete'
                    ></i>
                </div>
            );
        });
        return arr;
    };

    renderImg = (type) => {
        const { showImgCount, scrollIndex, HDScrollIndex } = this.state;
        let arr = [];
        let allUrl = [];
        if (type === 'bg') {
            arr.push(
                <div key='bing'>
                    <div className={s.defaultImg}></div>
                    <img
                        title='Bing壁纸，每日更新'
                        onError={this.onError}
                        onClick={this.changeBg.bind(
                            null,
                            'https://bing.biturl.top/?resolution=1920&format=image&index=0&mkt=zh-CN'
                        )}
                        className={s.bgImg}
                        alt='精美壁纸'
                        src={
                            'https://bing.biturl.top/?resolution=1920&format=image&index=0&mkt=zh-CN'
                        }
                    />
                    <div className={s.overlayer}></div>
                    <div className={s.title}>Bing壁纸，每日更新</div>
                </div>
            );
        }
        switch (type) {
            case 'bg': 
                allUrl = bgAllUrl;
                break;
            case '4k':
                allUrl = HDurl;
                break;
            default:
                allUrl = bgAllUrl;
        }
        for (let i = 0; i < allUrl.length; i++) {
            let url = allUrl[i];
            if (type === 'bg' && i > showImgCount * scrollIndex) break;
            if (type === '4k' && i > showImgCount * HDScrollIndex) break;
            arr.push(
                <div key={url}>
                    <div className={s.defaultImg}></div>
                    <img
                        onError={this.onError}
                        onClick={this.changeBg.bind(
                            null,
                            `${URLCDN}/${type}/${url}`
                        )}
                        className={`${s.bgImg} videoBg`}
                        alt='推荐壁纸'
                        src={`${URLCDN}/thumbnail/${url}`}
                    />
                    <div className={s.overlayer}></div>
                </div>
            );
        }
        return arr;
    };

    renderVideo = () => {
        const { showImgCount, videoScrollIndex } = this.state;
        let arr = [];
        for (let i = 0; i < videoUrl.length; i++) {
            let url = videoUrl[i];
            if (i > showImgCount * videoScrollIndex) break;
            arr.push(
                <div key={url}>
                    <div className={s.defaultImg}></div>
                    <video
                        loop={true}
                        muted={true}
                        onClick={this.changeBg.bind(
                            null,
                            `${URLCDN}/video/${url}`,
                            true
                        )}
                        onMouseOver={this.onMouseOver}
                        onMouseOut={this.onMouseOut}
                        onError={this.onVideoError}
                        className={s.videoBg}
                        alt='动态壁纸'
                        src={`${URLCDN}/video/${url}`}
                    />
                </div>
            );
        }
        return arr;
    };

    componentWillUnmount() {
        this.setTime && clearInterval(this.setTime);
    }

    componentDidMount() {
        const { videoScrollIndex, scrollIndex, HDScrollIndex } = this.state;

        this.setState({
            showImgCount:
                Math.ceil(document.body.offsetHeight / 190) + MORE_LOAD,
        });

        this.setTime = setTimeout(() => {
            this.setState({
                scrollIndex: scrollIndex + 1,
                videoScrollIndex: videoScrollIndex + 1,
                HDScrollIndex: HDScrollIndex + 1
            });
        }, 2000);

        let request = window.indexedDB.open('uploadDB');

        request.onupgradeneeded = (event) => {
            window.db = event.target.result;
            let objectStore;
            if (!window.db.objectStoreNames.contains('uploadTable')) {
                objectStore = window.db.createObjectStore('uploadTable', {
                    keyPath: 'id',
                });
                objectStore.createIndex('file', 'file', { unique: false });
            }
        };

        request.onerror = function (event) {
            console.log('数据库打开报错2');
        };

        request.onsuccess = (event) => {
            window.db = request.result;
            this.readAll();
        };
    }

    readAll = () => {
        let objectStore = window.db
            .transaction('uploadTable')
            .objectStore('uploadTable');
        let arr = [];
        let lastId = 0;
        objectStore.openCursor().onsuccess = (event) => {
            let cursor = event.target.result;

            if (cursor) {
                let src = window.URL.createObjectURL(cursor.value.file)
                arr.unshift({ id: cursor.key, src ,file: cursor.value.file });
                lastId = cursor.key;
                cursor.continue();
            } else {
                this.setState({
                    uploadImgs: arr,
                    lastId,
                });
            }
        };
    };

    addImgToDB = (data, newArr) => {
        const { lastId } = this.state;
        const { setProps } = this.props;
        let request = window.db
            .transaction(['uploadTable'], 'readwrite')
            .objectStore('uploadTable')
            .add({ id: lastId + 1, file: data });

        request.onsuccess = (event) => {
            this.changeBg(newArr[0].src, null,lastId + 1);
            this.setState({
                uploadImgs: newArr,
                lastId: lastId + 1,
            });
            // console.log('数据写入成功')
            setProps('bgImgFile',data)
        };

        request.onerror = function (event) {
            let message = _.get(event, 'target.error.message', '上传失败');
            
            if (
                message ===
                "Unable to add key to index 'src': at least one key does not satisfy the uniqueness requirements."
            ) {
                message = '图片已存在';
            }
            window.notification.notice({ content: <Info>{message}</Info> });
        };
    };

    deleteImgFromDB = (id) => {
        const { uploadImgs } = this.state;
        let dom = document.getElementById('uploadingImg-' + id);
        dom.style.opacity = 0;
        dom.style.height = 0;

        window.db
            .transaction(['uploadTable'], 'readwrite')
            .objectStore('uploadTable')
            .delete(id);

        _.remove(uploadImgs, (item) => {
            return item.id === id;
        });

        setTimeout(() => {
            this.setState({
                uploadImgs,
            });
        }, 660);
    };

    setIndex = (index) => {
        this.setState({
            targetIndex: index,
        });
    };

    uploading = (e) => {
        const { uploadImgs } = this.state;
        let file = e.target.files && e.target.files[0];
        if (undefined === file) {
            return;
        }
        
        const { lastId } = this.state;
        let url = window.URL.createObjectURL(file)
        let newArr = [{ id: lastId + 1, src: url,file}, ...uploadImgs];
        this.addImgToDB(file, newArr);
    };

    render() {
        const { show } = this.props;
        const { targetIndex } = this.state;
        let containerStyle = classnames({
            [s.container]: true,
            [s.show]: show,
        });

        return (
            <article className={containerStyle} onScroll={this.onScroll}>
                <header>
                    <img
                        className={targetIndex === 0 ? s.show : ''}
                        onClick={this.setIndex.bind(null, 0)}
                        src={library}
                        alt='推荐壁纸'
                        title='推荐壁纸'
                    />
                    <img
                        className={targetIndex === 1 ? s.show : ''}
                        onClick={this.setIndex.bind(null, 1)}
                        style={{
                            width: '27px',
                            position: 'relative',
                            top: '2px',
                        }}
                        src={hd}
                        alt='高清壁纸'
                        title='高清壁纸'
                    />
                    {window.userTerminal === 'pc' && <img
                        className={targetIndex === 2 ? s.show : ''}
                        style={{
                            width: '27px',
                            position: 'relative',
                            top: '2px',
                        }}
                        onClick={this.setIndex.bind(null, 2)}
                        src={video1}
                        alt='动态壁纸'
                        title='动态壁纸'
                    />}
                    <img
                        className={`${s.uploadingImg} ${
                            targetIndex === 3 ? s.show : ''
                        }`}
                        onClick={this.setIndex.bind(null, 3)}
                        src={uploading2}
                        alt='上传壁纸'
                        title='上传壁纸'
                    />
                </header>
                <div
                    className={s.box}
                    style={{ left: `${targetIndex * -340}px` }}
                >
                    <div className={s.body}>
                        <section id='bgConfig'>{this.renderImg('bg')}</section>
                    </div>
                    <div className={s.body}>
                        <section id='bgConfig'>{this.renderImg('4k')}</section>
                    </div>
                    <div className={s.body}>
                        <section>{this.renderVideo()}</section>
                    </div>

                    <div className={s.body}>
                        <div className={s.uploading}>
                            <input
                                type='file'
                                accept='image/*'
                                onChange={this.uploading}
                            />
                            <img
                                alt='上传图片'
                                src={uploading}
                            />
                            <span>拖拽到此处或</span>
                            <button>点击上传</button>
                        </div>
                        {this.renderUploadImgs()}
                    </div>
                </div>
            </article>
        );
    }
}

export default Config;
