import React from 'react'
import s from './styles.module.scss'
import Modal from '../Modal'
import Button from '../Button'
import _ from 'lodash'
import { Info } from '../notification'

class AddNav extends React.Component {
    constructor(props) {
        super(props)
        this.urlInput = React.createRef()
        this.titleInput = React.createRef()
    }

    getIconUrl = (url) => {
        let reg = new RegExp(/((\w+):\/\/)?([^/:]+)(:\d*)?/)
        let matchObj = url.match(reg)
        url = (matchObj && matchObj[0]) || ''
        return url
    }

    onClickBtn = () => {
        const { userNavIndexArr, setProps } = this.props
        let url = this.urlInput.current.value.trim()
        if (!url) {
            window.notification.notice({ content: <Info>请输入网址</Info> })
            this.urlInput.current.focus()
            return
        }
        let title = this.titleInput.current.value.trim()
        title = title || url
        let userNav = localStorage.getItem('userNav')
        userNav = (userNav && JSON.parse(userNav)) || {}

        for (let key in userNav) {
            if (userNav[key].url === url) {
                window.notification.notice({ content: <Info>网址已添加</Info> })
                this.urlInput.current.focus()
                return
            }
        }
        let id = 'user-' + Object.keys(userNav).length
        localStorage.setItem(
            'userNav',
            JSON.stringify({
                ...userNav,
                [id]: {
                    title,
                    url,
                    icon: this.getIconUrl(url) + '/favicon.ico',
                },
            })
        )
        let newNavIndexArr = [...userNavIndexArr, id]
        localStorage.setItem('userNavIndexArr', JSON.stringify(newNavIndexArr))
        setProps('userNavIndexArr', newNavIndexArr)
        window.notification.notice({ content: <Info>添加成功</Info> })
        this.urlInput.current.value = ''
        this.titleInput.current.value = ''
    }

    onClickSave = () => {
        const { userNavIndexArr, editIndex } = this.props
        let url = this.urlInput.current.value.trim()
        if (!url) {
            window.notification.notice({ content: <Info>请输入网址</Info> })
            this.urlInput.current.focus()
            return
        }
        let title = this.titleInput.current.value.trim()
        title = title || url
        localStorage.removeItem(this.oldTitle + this.oldUrl)
        let userNav = localStorage.getItem('userNav')
        userNav = (userNav && JSON.parse(userNav)) || {}
        let newUserNav = {
            ...userNav,
            [userNavIndexArr[editIndex]]: {
                title,
                url,
                icon: this.getIconUrl(url) + '/favicon.ico',
            },
        }
        localStorage.setItem('userNav', JSON.stringify(newUserNav))
        window.notification.notice({ content: <Info>保存成功</Info> })
    }

    render() {
        const { show, setProps, editIndex, useNavs } = this.props

        if (!_.isNull(editIndex) && !this.urlInput.current.value) {
            this.urlInput.current.value = useNavs[editIndex].url
            this.titleInput.current.value = useNavs[editIndex].title
            this.oldTitle = useNavs[editIndex].title
            this.oldUrl = useNavs[editIndex].url
        }

        return (
            <Modal setProps={setProps} show={show} title={'快捷导航设置'}>
                <div className={s.container}>
                    <div className={s.title}>
                        {_.isNull(editIndex) ? '添加网址' : '修改网址'}
                    </div>
                    <div className={s.row}>
                        <label>网址</label>
                        <input
                            ref={this.urlInput}
                            maxLength="3000"
                            type="text"
                            placeholder="输入或粘贴网址Url，协议默认https://"
                            autoComplete="off"
                        />
                        <div className={s.underline}></div>
                    </div>
                    <div className={s.row}>
                        <label>名称</label>
                        <input
                            ref={this.titleInput}
                            maxLength="3000"
                            type="text"
                            placeholder="输入网站名称"
                            autoComplete="off"
                        />
                        <div className={s.underline}></div>
                    </div>
                    <div className={s.btnBox}>
                        {_.isNull(editIndex) ? (
                            <Button value={'添加'} onClick={this.onClickBtn} />
                        ) : (
                            <Button value={'保存'} onClick={this.onClickSave} />
                        )}
                    </div>
                    {/* <div className={`${s.title} ${s.titleMargin}`}>推荐</div>       */}
                </div>
            </Modal>
        )
    }
}

export default AddNav
