import React, {useRef, useEffect} from 'react'
import _ from 'lodash'
import s from './styles.module.scss'
import classnames from 'classnames'

function ConfigMenu(props) {
    const menuRef = useRef();
    menuRef.current = props.show
    const onBodyClick = (e) => {
        if (menuRef.current) {
            let i = 0
            let target = e.target
            let setId = _.get(target, 'dataset.id', '')

            if (setId === 'setBtn') return
            while (i < 2) {
                //menu 下只有两级子元素
                let id = _.get(target, 'parentElement.dataset.id', '')
                if (id === 'configMenu') {
                    return
                } else {
                    i++
                    target = target.parentElement || {}
                }
            }
            props.setProps('showConfigMenu' , false)
        }
    }
    useEffect(()=> {
        document.body.addEventListener('click', onBodyClick)
        
        return () => {
            document.body.removeEventListener('click', onBodyClick)
        }
    },[])

    const showTemplate = () => {
        props.setProps('showConfigMenu' , false, 'showTemplateConfig', true)
    }

    const signOut = () => {
        localStorage.removeItem('token')
        localStorage.removeItem('userName')
        localStorage.removeItem('userIcon')
        props.setProps('showConfigMenu' , false, 'userName' , '', 'userIcon', '')
    }

    let menu = classnames({
        [s.menu]: true,
        [s.showMenu]: props.show,
    })
    
    return (
        <div
            data-id="configMenu"
            className={menu}
            ref={menuRef}
        >
            <div className={s.menuItem} onClick={showTemplate}>
                <i className={'iconfont icon-fenxiang'}></i>
                <span>分享模板</span>
            </div>
            <div className={s.menuItem} onClick={signOut}>
                <i className={'iconfont icon-fenxiang'}></i>
                <span>退出登录</span>
            </div>
        </div>
    )
}

export default ConfigMenu
