import React from 'react'
import s from './styles.module.scss'
import Modal from '../Modal'
import Button from '../Button'
import _ from 'lodash'
import { Info } from '../notification'
import classnames from 'classnames'

class TemplateConfig extends React.Component {
    constructor(props) {
        super(props)
        this.nameInput = React.createRef()
        this.navCheckbox = React.createRef()
        this.bgCheckbox = React.createRef()
        this.noteCheckbox = React.createRef()
        this.templateUrl = React.createRef()
        this.initGet = false
        this.state = {
            nameError: false,
            errorMes: '',
            templateUrl: '',
            nameValue:'',
            navCheckbox:true,
            bgCheckbox:true,
            noteCheckbox:'',
            updatedAt: ''
        }
    }

    getIconUrl = (url) => {
        let reg = new RegExp(/((\w+):\/\/)?([^/:]+)(:\d*)?/)
        let matchObj = url.match(reg)
        url = (matchObj && matchObj[0]) || ''
        return url
    }

    onCreate = () => {
        const {bgCheckbox, navCheckbox, noteCheckbox, updatedAt} = this.state
        let name = this.nameInput.current.value.trim()
        if (!name) {
            this.setState({
                errorMes: '请输入模板名称',
                nameError: true,
            })
            this.nameInput.current.focus()
            return
        }

        let userNav = localStorage.getItem('userNav')
        userNav = (userNav && JSON.parse(userNav)) || {}

        let userNavIndexArr = localStorage.getItem('userNavIndexArr')
        userNavIndexArr = (userNavIndexArr && JSON.parse(userNavIndexArr)) || {}

        let bgVideoUrl = localStorage.getItem('videoUrl')
        let bgImgUrl = localStorage.getItem('bgUrl')
        let imgDBid = localStorage.getItem('imgDBid')
        let bgImgFile = null
        let body = { name, uid: '1', type: 'user' }
        if (imgDBid) {
            let formData = new FormData();
            console.log(this.props.bgImgFile);
            
            formData.append('picture', this.props.bgImgFile);
            bgImgFile = formData
            
        }
        if (this.navCheckbox.current.checked) {
            body.userNav = userNav
            body.userNavIndexArr = userNavIndexArr
        }
        if (this.bgCheckbox.current.checked) {
            body.bgVideoUrl = bgVideoUrl
            body.bgImgUrl = bgImgUrl
            body.bgImgFile = bgImgFile
        }
        if (!bgCheckbox && !navCheckbox && !noteCheckbox) {
            this.setState({
                errorMes: '请选择模板分享内容',
            })
            return
        }


        let formData = new FormData();

        imgDBid && formData.append('picture', bgImgFile);
        fetch('/api/v1/template/upload', {
            method: 'post',
            body: formData,
        })
            .then((res) => res.json())
            .then((json) => {
                
            })

        
        fetch('/api/v1/template/save', {
            method: 'post',
            body: JSON.stringify(body),
            headers: new Headers({
                'Content-Type': 'application/json',
            }),
        })
            .then((res) => res.json())
            .then((json) => {
                if (json.code === '20000') {
                    this.setState({
                        templateUrl: `${window.location.origin}/${name}`,
                        updatedAt: json.updatedAt
                    })
                    window.notification.notice({ content: <Info>{updatedAt ? '模板更新成功' : '模板创建成功'}</Info> })

                } else if (json.code === '30000') {
                    this.setState({
                        errorMes: json.message,
                    })
                    window.notification.notice({ content: <Info>{updatedAt ? '模板更新失败' : '模板创建失败'}</Info> })
                }
            })
    }

    checkName = (e) => {
        let value = e.target.value || ''
        value = value.trim()
        fetch(`/api/v1/template/checkName?name=${value}&uid=${1}`, {})
            .then((res) => res.json())
            .then((data) => {
                if (data.code === '30000') {
                    this.setState({
                        nameError: true,
                        errorMes: data.message,
                    })
                }
            })
            .catch((err) => {
                console.log(err)
            })
    }

    onNameChange = (e) => {
        const { nameError } = this.state
        this.setState({
            nameValue: e.target.value
        })
        if (nameError) {
            this.setState({
                nameError: false,
                errorMes: '',
            })
        }
    }

    onChangeCheckbox = (type, e) => {
        const { errorMes } = this.state
        this.setState({
            [type]: e.target.checked
        })
        if (errorMes) {
            this.setState({
                errorMes: '',
            })
        }
    }

    fetchTemplate() {
        this.initGet = true
        fetch(`/api/v1/template/get?uid=${1}`, {})
            .then((res) => res.json())
            .then((data) => {
                this.setState({
                    templateUrl: `${window.location.origin}?tmp=${data.name}`,
                    nameValue:data.name,
                    navCheckbox:!!data.userNavIndexArr || !!data.userNav,
                    bgCheckbox:!!data.bgImgUrl || !!data.bgVideoUrl || !!data.bgImgFile,
                    noteCheckbox:!!data.note,
                    updatedAt: data.updatedAt
                })
            })
            .catch((err) => {
                console.log(err)
            })
    }

    copy = (url) => {
        this.templateUrl.current.select()
        document.execCommand("Copy")
    }

    render() {
        const { show, setProps } = this.props
        const { nameError, errorMes, templateUrl, updatedAt, nameValue,bgCheckbox, navCheckbox, noteCheckbox } = this.state
        if (show && !this.initGet) {
            this.fetchTemplate()
        }

        let nameline = classnames({
            [s.underline]: true,
            [s.errorLine]: nameError,
        })

        return (
            <Modal setProps={setProps} show={show} title={'模板设置'}>
                <div className={s.container}>
                    <div className={s.row} >
                        <label>名称</label>
                        <input
                            value={nameValue}
                            onChange={this.onNameChange}
                            className={s.name}
                            ref={this.nameInput}
                            maxLength='64'
                            type='text'
                            placeholder='输入模板名称'
                            autoComplete='off'
                            onBlur={this.checkName}
                        />
                        <div className={nameline}></div>
                    </div>
                    <div className={s.row}>
                        <label>分享内容</label>
                        <input
                            checked={navCheckbox}
                            ref={this.navCheckbox}
                            onChange={this.onChangeCheckbox.bind(null,'navCheckbox')}
                            className={s.checkbox}
                            type='checkbox'
                            name='favorite'
                            value='userNav'
                        />
                        <span className={s.checkboxVal}>快捷导航</span>
                        <input
                            checked={bgCheckbox}
                            ref={this.bgCheckbox}
                            onChange={this.onChangeCheckbox.bind(null,'bgCheckbox')}
                            className={s.checkbox}
                            type='checkbox'
                            name='favorite'
                            value='bg'
                        />
                        <span className={s.checkboxVal}>壁纸</span>

                        <input
                            checked={noteCheckbox}
                            ref={this.noteCheckbox}
                            onChange={this.onChangeCheckbox.bind(null,'noteCheckbox')}
                            type='checkbox'
                            className={s.checkbox}
                            name='favorite'
                            value='note'
                        />
                        <span className={s.checkboxVal}>便签</span>
                    </div>
                    {templateUrl && (
                        <div className={s.row}>
                            <label>分享链接</label>
                            <span className={s.templateUrl}>{templateUrl}</span>
                            <input type="text" ref={this.templateUrl} className={s.hide} readOnly value={templateUrl} />
                            <i title="复制链接" onClick={this.copy.bind(null,templateUrl)} className="iconfont icon-fuzhilianjie"></i>
                        </div>
                    )}
                    {updatedAt && (
                        <div className={s.row}>
                            <label>更新时间</label>
                            {updatedAt.substr(0)}
                            
                        </div>
                    )}
                    <div className={s.errorBox}>{errorMes}</div>
                    <div className={s.btnBox}>
                        {!updatedAt ? (
                            <Button
                                value={'生成模板'}
                                onClick={this.onCreate}
                            />
                        ) : (
                            <Button
                                value={'更新模板'}
                                onClick={this.onCreate}
                            />
                        )}
                    </div>
                    {/* <div className={`${s.title} ${s.titleMargin}`}>推荐</div>       */}
                </div>
            </Modal>
        )
    }
}

export default TemplateConfig
