import React from 'react'
import { ICONCOLORS, ERRORIMGURL } from '@/constant'
import s from './navItem.module.scss'
import { DragSource, DropTarget } from 'react-dnd'
import navTypes from '@/constant/types'

const navSource = {
    /**
     * 开始拖拽时触发当前函数
     * @param {*} props 组件的 props
     */
    beginDrag(props, monitor, component) {
        // 返回的对象可以在 monitor.getItem() 中获取到
        const dom = component.nav.current
        dom.style.opacity = 0.3
        return {
            navid: props.navid,
            x: dom.offsetLeft,
            y: dom.offsetTop,
            sourceDom: dom,
        }
    },

    /**
     * 拖拽结束时触发当前函数
     * @param {*} props 当前组件的 props
     * @param {*} monitor DragSourceMonitor 对象
     */
    endDrag(props, monitor, component) {
        // 当前拖拽的 item 组件
        const item = monitor.getItem()
        // 拖拽元素放下时，drop 结果
        const dom = component.nav.current
        dom.style.opacity = 1
        if (props.navid && window.navid && props.navid !== window.navid) {
            const { userNavIndexArr, setProps } = props
            let count = 0
            for (let i = 0; i < userNavIndexArr.length && count < 2; i++) {
                if (userNavIndexArr[i] === props.navid) {
                    userNavIndexArr[i] = window.navid
                    count++
                } else if (userNavIndexArr[i] === window.navid) {
                    userNavIndexArr[i] = props.navid
                    count++
                }
            }
            if (window.navMoveDom) {
                window.navMoveDom.style.transition = 'all 0s'
                window.navMoveDom.style.left = ''
                window.navMoveDom.style.top = ''
                window.navid = undefined
            }
            item.sourceDom.style.transition = 'all 0s'
            item.sourceDom.style.left = ''
            item.sourceDom.style.top = ''
            let newUserNavIndexArr = [...userNavIndexArr]
            setProps('userNavIndexArr', newUserNavIndexArr)
            localStorage.setItem('userNavIndexArr', JSON.stringify(newUserNavIndexArr))
            setTimeout(()=>{
                item.sourceDom.style.transition = 'all .25s'
                window.navMoveDom.style.transition = 'all .25s'
                window.navMoveDom = undefined
                item.sourceDom = undefined
            })
        }
    },
}

const navTarget = {
    // 当有对应的 drag source 放在当前组件区域时，会返回一个对象，可以在 monitor.getDropResult() 中获取到
    drop: (props, monitor, component) => {
        return { navid: props.navid }
    },
    hover: (props, monitor, component) => {
        /**
         * 拖拽延迟响应，有卡顿暂时取消
         */
        // const item = monitor.getItem()
        // if (item.navid === props.navid) return
        
        // if (!window.navHoverDelay) {
        //     window.navHoverDelay = {
        //         id: props.navid,
        //         ready: false,
        //         timeout: setTimeout(()=>{
        //             window.navHoverDelay.ready = true
        //         },250)
        //     }
        //     return
        // }else if (window.navHoverDelay.id !== props.navid) {
        //     clearInterval(window.navHoverDelay.timeout)
        //     window.navHoverDelay = {
        //         id: props.navid,
        //         ready: false,
        //         timeout: setTimeout(()=>{
        //             window.navHoverDelay.ready = true
        //         },250)
        //     }
        //     return
        // }else if (window.navHoverDelay.ready) {
        //     window.navHoverDelay = undefined
        // }else {
        //     return
        // }

        if ((window.navsMoving && window.navsMoving[props.navid])) return
        const item = monitor.getItem()
        if (item.navid === props.navid) return
        const targetDom = props.navRef.current
        const sourceDom = item.sourceDom
        if (window.navMoveDom) {
            window.navMoveDom.style.left = ''
            window.navMoveDom.style.top = ''
        }
        window.navMoveDom = targetDom
        window.navid = props.navid
        let left = item.x - targetDom.offsetLeft
        let top = item.y - targetDom.offsetTop

        sourceDom.style.left = -left + 'px'
        sourceDom.style.top = -top + 'px'
        targetDom.style.left = left + 'px'
        targetDom.style.top = top + 'px'
        window.navsMoving = window.navsMoving || {}
        window.navsMoving[props.navid] = setTimeout(() => {
            window.navsMoving[props.navid] = null;
        }, 300);  //300毫秒内不在响应hover

        return {
            before: props.navRef.current,
        }
    },
}

@DropTarget(navTypes.NAV, navTarget, (connect, monitor) => ({
    // 包裹住 DOM 节点，使其可以接收对应的拖拽组件
    connectDropTarget: connect.dropTarget(),
    // drag source是否在 drop target 区域
    isOver: monitor.isOver(),
    // 是否可以被放置
    canDrop: monitor.canDrop(),
}))
class Icon extends React.Component {
    constructor(props) {
        super(props)
        this.icon = React.createRef()
    }
    render() {
        const { connectDropTarget, src, onError } = this.props
        return (
            connectDropTarget &&
            connectDropTarget(
                <div ref={this.icon} className={s.imgBox}>
                    <img alt='' src={src} onError={onError} />
                </div>
            )
        )
    }
}

@DragSource(
    // type 标识，这里是字符串 'box'
    navTypes.NAV,
    // 拖拽事件对象
    navSource,
    // 收集功能函数，包含 connect 和 monitor 参数
    // connect 里面的函数用来将 DOM 节点与 react-dnd 的 backend 建立联系
    (connect, monitor) => ({
        // 包裹住 DOM 节点，使其可以进行拖拽操作
        connectDragSource: connect.dragSource(),
        // 是否处于拖拽状态
        isDragging: monitor.isDragging(),
    })
)
class NavItem extends React.Component {
    constructor(props) {
        super(props)
        this.nav = React.createRef()
    }
    getUrl(url) {
        let http = url.substr(0, 4).toLocaleLowerCase()
        if (http !== 'http') {
            url = 'https://' + url
        }
        return url
    }

    onError = (tiem, e) => {
        const rand = Math.floor(Math.random() * ICONCOLORS.length)
        let src = localStorage.getItem(tiem.title + tiem.url)

        if (!src && tiem.title) {
            src = `https://iph.href.lu/128x128?bg=${ICONCOLORS[rand].bg}&fg=${ICONCOLORS[rand].fg}&text=${tiem.title[0]}`
            localStorage.setItem(tiem.title + tiem.url, src)
        }
        if (e.target.src !== src) {
            e.target.src = src
        } else {
            e.target.src = ERRORIMGURL
        }
    }

    render() {
        const {
            nav,
            navid,
            onContextMenu,
            connectDragSource,
            userNavIndexArr,
            setProps,
        } = this.props

        return (
            connectDragSource &&
            connectDragSource(
                <a
                    href={this.getUrl(nav.url)}
                    target='_blank'
                    rel='noopener noreferrer'
                    key={nav.url + nav.title}
                    className={s.navItem}
                    onContextMenu={onContextMenu}
                    data-navid={navid}
                    ref={this.nav}
                >
                    <Icon
                        setProps={setProps}
                        userNavIndexArr={userNavIndexArr}
                        navid={navid}
                        navRef={this.nav}
                        src={this.getUrl(nav.icon)}
                        onError={this.onError.bind(null, nav)}
                    />
                    <div className={s.title} title={nav.title}>
                        {nav.title}
                    </div>
                </a>
            )
        )
    }
}

export default NavItem
