import React from 'react';
import s from './styles.module.scss';
class Time extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    weeks = (week) => {
        let {language} = this.props;
        let weeksEnglish = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
        let weeksChinese = [ "星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"];
        if(language === "chinese"){
            for(let i = 0; i < weeksChinese.length; i++){
                if(i === week){
                    return weeksChinese[i];
                }
            }
        }else{
            for(let i = 0; i < weeksEnglish.length; i++){
                if(i === week){
                    return weeksEnglish[i];
                }
            }
        }
        
    }
    mouths = (mouth) => {
        let mouthsEnglish = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
        let mou = mouth - 1;
        for(let i = 0; i < mouthsEnglish.length; i++){
            if(i === mou){
                return mouthsEnglish[i];
            }
        }
    }

    componentDidMount() {
        this.setInterval = setInterval(()=>{
            const {count} = this.state;
            this.setState({
                count: count+1
            })
        },10000)
    }

    componentWillUnmount() {
        clearInterval(this.setInterval)
    }
    
    render(){
        let {language} = this.props;
        let day = new Date(); 
        let year = day.getFullYear();
        let month = day.getMonth() + 1;
        let mon = language === "chinese" ? month : this.mouths(month);
        let data = day.getDate();
        let week = day.getDay();
        let everyWeek = this.weeks(week);
        let hours = day.getHours();
        let minute = day.getMinutes();
        return(
            <div className={s.container}>
                <div>
                    <div className={s.time}>{`${hours}:${minute / 10 < 1 ? '0' + minute : minute}`}</div>
                    {language === "chinese" && <div className={s.data}>{`${year}年`} {`${month}月`} {`${data}日`} {`${everyWeek}`}</div>}
                    {language === "english" && <div className={s.data}>{`${everyWeek}`} , {`${mon}`} {`${data}`} , {`${year}`}</div>}
                </div>
            </div>
        );
    }
}

export default Time;